import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
const routes = [
  {
    path: '/',
    name: 'login',
    component: () => import('../views/LoginView.vue')
  },
  {
    path: '/home',
    name: 'home',
    component: () => import('../views/HomeView.vue'),
    redirect:"/homeinfo",
    children: [
      {
        path: "/charging",
        name: "charging",
        component: () => import("../views/ChargingView.vue")
      },
      {
        path: "/number",
        name: "number",
        component: () => import("../views/NumbermanagementView.vue")
      },
      {
        path: "/authority",
        name: "authority",
        component: () => import('../views/AuthorityView.vue')
      },
      {
        path: '/business',
        name: 'business',
        component: () => import('../views/BusinessView.vue'),
      },
      {
        path: "/homeinfo",
        name: 'homeinfo',
        component: () => import('../views/HomeDataView.vue'),
        //路由重定向
        redirect:"/enterprisedata",
        children: [
          {
            // 子路由 企业数据
            path: '/enterprisedata',
            name: 'enterprisedata',
            component: () => import('../views/EntDataView.vue')
          },
          {
            path: '/Serverdata',
            name: 'Serverdata',
            component: () => import('../views/ServerDataView.vue')
          },
          {
            path: '/Provdata',
            name: 'Provdata',
            component: () => import('../views/ProvdataView.vue')
          }
        ]
      }
    ]
  },
]

const router = new VueRouter({
  routes
})

export default router
